import { API_URL } from '../config/api';

class CustomerService {
  // Create customer
  static async createCustomer(customerData, parentPromoterId) {
    try {
      const response = await fetch(`${API_URL}/customer/create`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ...customerData,
          parentPromoterId: parentPromoterId
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Failed to create customer');
      }

      return data;
    } catch (error) {
      console.error('Customer creation error:', error);
      throw error;
    }
  }

  // Update customer
  static async updateCustomer(customerId, customerData) {
    try {
      const response = await fetch(`${API_URL}/customer/update`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          customerId,
          ...customerData
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Failed to update customer');
      }

      return data;
    } catch (error) {
      console.error('Customer update error:', error);
      throw error;
    }
  }

  // Delete customer
  static async deleteCustomer(customerId) {
    try {
      const response = await fetch(`${API_URL}/customer/delete`, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          customerId
        })
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Failed to delete customer');
      }

      return data;
    } catch (error) {
      console.error('Customer deletion error:', error);
      throw error;
    }
  }

  // Get customer by ID
  static async getCustomerById(customerId) {
    try {
      const response = await fetch(`${API_URL}/customer/${customerId}`, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Failed to get customer');
      }

      return data;
    } catch (error) {
      console.error('Get customer error:', error);
      throw error;
    }
  }

  // Get all customers
  static async getAllCustomers(promoterId = null) {
    try {
      const url = promoterId
        ? `${API_URL}/customer/list?promoterId=${promoterId}`
        : `${API_URL}/customer/list`;

      const response = await fetch(url, {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        }
      });

      const data = await response.json();

      if (!response.ok || !data.success) {
        throw new Error(data.message || 'Failed to get customers');
      }

      return data;
    } catch (error) {
      console.error('Get customers error:', error);
      throw error;
    }
  }
}

export default CustomerService;
